              if (data === null) {
                 return
              }
              if (data.status === 200) {
                this.packageList = data.data.products;
                if (!this.isPc) {
                  this.packageList.map((x, index) => {


                    switch (x.pid) {
                      case 23:
                        x.name = '200G UNLIMITED';
                        x.description = 'First 200G data at uncapped speeds';
                        break;
                      case 21:
                        x.name = '100G UNLIMITED';
                        x.description = 'First 100G data at uncapped speeds';
                        break;
                      case 22:
                        x.name = '50G UNLIMITED';
                        x.description = 'First 50G data at uncapped speeds';
                        break;
                      default:
                        break;
                    }

                    // setup the recommend
                    if (x.name.indexOf('50G') > -1) {
                      x['recommend'] = 10;
                    } else if (x.name.indexOf('100G') > -1) {
                      x['recommend'] = 66;
                    } else {
                      x['recommend'] = 24;
                    }
                  });

