            'billingcycle': this.selectBillingCycle.cycleType,
          })
            .then(
              (data) => {
                this.isPaying = false;
                if (data.data.result_code === 200) {
                  this.firebaselogger.logEvent(this.firebaselogger.eventConfig.BEGIN_CHECKOUT, {
                    PID: this.packageList[this.selectedMethod].pid,
                    NAME: this.packageList[this.selectedMethod].name,
                    INTERVAL: this.selectBillingCycle.cycleType,
                    PRICE: this.packageList[this.selectedMethod].payPreferentialPrice,
                    CURRENCY: 'USD',
                    QUANTITY: 1,
                    DEVICES: this.deviceNumber,
                    BANDWIDTH: this.bandwidthNumber,
                    CHECKOUT_OPTION: this.payMethodList[this.selectedMethod].type
                  });
                  this.$router.push({
