              'email': this.email,
              'paymentMethod': this.payMethodList[this.selectedMethod].type,
              'productId': this.packageList.id,
              'billingcycle': this.selectBillingCycle.cycleType,
              'aff': this.$route.query['aff'] || localStorage.getItem('aff') || '',
              'redirectTo': `${window.location.origin}/order/generate-order`,
              'configoptions': this.configoptions
            })
              .then(
                (data) => {
                  this.isPaying = false;
                  if (data && (data.status === 200 || data.status === 201)) {
                    this.$router.push({
                      path: '/order/confirm-order',
                      name: 'pay-success',
                      params: {
                        account: this.email,
                        packages: this.packageList[this.selectedPackage].name,
                        cycle: this.selectBillingCycle.name,
                        originalPrice: this.packageList[this.selectedPackage].payOriginalPrice,
                        reduce: this.packageList[this.selectedPackage].payReduce,
                        preferentialPrice: this.packageList[this.selectedPackage].payPreferentialPrice,
                        aff: this.$route.query['aff'] || localStorage.getItem('aff') || '',
                        paymentMethod: this.payMethodList[this.selectedMethod].type,
                        productId: this.packageList.id,
                        billingcycle: this.selectBillingCycle.cycleType,
                        redirectTo: `${window.location.origin}/order/generate-order`,
                        configoptions: this.configoptions
                      }
                    });
                  } else {
                    if (data.message.indexOf('registered') > -1) {
                      this.errorDescription = '';
                      this.errorDisplay = true;
                      this.scrollToEmailPosition();
                    } else {
                      this.errorDescription = '';
                      this.bottomErrorDisplay = true;
                      this.scrollToBottom();
                    }
                  }
                },
              );
          } else {
            this.errorDescription = 'name@example.com';
            this.scrollToEmailPosition();
          }
        }
      },
      scrollToBottom() {
        const nowTop = window.scrollY;
        this.scrollToWithAnimation(nowTop, nowTop + 30, 2500);
      },
      scrollToEmailPosition() {
        const nowTop = window.scrollY;
        const emailTop = document.getElementsByClassName('input-wrap')
                .item(0).offsetTop;
        this.scrollToWithAnimation(nowTop, emailTop - 180, 2500);
      },
      selectSelfPackage(index, item) {
        this.selectDropPackage = false;
        this.selectBillingCycle = item;
        this.calculatePrice(item.cycleType);
      },
      selectSelfDate(e, text) {
        this.selectDropDate = false;
        this.selectSelfDateText = text;
        this.selectedPackage = 4;
      },
      stepOneClick(e) {
        this.selectedPackage = e;
        // scroll animation
        const nowTop = window.scrollY;
        const submitTop = document.getElementsByClassName('next-step')
          .item(0).offsetTop;
        this.scrollToWithAnimation(nowTop, submitTop - 70, 2500);
        // packageInfo
        this.calculatePrice(this.selectBillingCycle.cycleType);
        this.handlePackageInfo(e);
        // Send the event to Firebase on Android device
        if (localStorage.getItem('inapp') === 'android') {
          this.firebaselogger.logEvent(this.firebaselogger.eventConfig.SELECT_CONTENT, {
            PID: this.packageList[e].pid,
            NAME: this.packageList[e].name,
            INTERVAL: this.selectBillingCycle.cycleType,
            PRICE: this.packageList[e].payPreferentialPrice,
            CURRENCY: 'USD'
          });
        }
      },
      handlePackageInfo(e) {
        //
        this.packageList.id = this.packageList[e].pid;
      },
      calculatePrice(cycleType) {
        if (!this.isPc) {
          this.configoptionsBuf = [];
          this.packageList.map((item, index) => {
            // get the device limit price
            let devicePrice = 0;
            let devicePriceMonthly = 0;
            let bandwidth = 0;
            let bandwidthMonthly = 0;
            item['configoptions']['configoption'].map(config => {
              if (config.name.indexOf('Device Limit') > -1) {
                devicePrice = Number(config['options']['option'][0]['pricing']['USD'][cycleType]) * this.deviceNumber;
                devicePriceMonthly = Number(config['options']['option'][0]['pricing']['USD']['monthly']) * this.deviceNumber;
                // get the device limit and addon bandwidth
                if (index === this.selectedPackage) {
                  this.configoptionsBuf[0] = config.id;
                  this.configoptionsBuf[1] = this.deviceNumber;
                }
              }
              if (config.name.indexOf('Addon Bandwidth') > -1) {
                bandwidth = Number(config['options']['option'][0]['pricing']['USD'][cycleType]) * this.bandwidthNumber;
                bandwidthMonthly = Number(config['options']['option'][0]['pricing']['USD']['monthly']) * this.bandwidthNumber;
                if (index === this.selectedPackage) {
                  this.configoptionsBuf[2] = config.id;
                  this.configoptionsBuf[3] = this.bandwidthNumber;
                }
              }
            });
            // calculate the price
            switch (cycleType) {
              case 'monthly':
                item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                item.preferentialPrice = this.mathRound(item.originalPrice);
                item.payReduce = 0;
                item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly) + devicePriceMonthly + bandwidthMonthly);
                item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.monthly) + devicePrice + bandwidth);
                item.monthly = item.payPreferentialPrice;
                break;
              case 'semiannually':
                item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                item.preferentialPrice = this.mathRound(Number(item.pricing.USD.semiannually * 2) + devicePrice * 2 + bandwidth * 2);
                item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly * 6) + devicePriceMonthly * 6 + bandwidthMonthly * 6);
                item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.semiannually) + devicePrice + bandwidth);
                item.payReduce = this.mathRound(item.payOriginalPrice - item.payPreferentialPrice);
                item.monthly = this.mathRound(item.payPreferentialPrice / 6);
                break;
              case 'quarterly'://
                item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                item.preferentialPrice = this.mathRound(Number(item.pricing.USD.quarterly * 4) + devicePrice * 4 + bandwidth * 4);
                item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly * 3) + devicePriceMonthly * 3 + bandwidthMonthly * 3);
                item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.quarterly) + devicePrice + bandwidth);
                item.payReduce = this.mathRound(item.payOriginalPrice - item.payPreferentialPrice);
                item.monthly = this.mathRound(item.payPreferentialPrice / 3);
                break;
              case 'annually':
                item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                item.preferentialPrice = this.mathRound(Number(item.pricing.USD.annually) + devicePrice + bandwidth);
                item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.annually) + devicePrice + bandwidth);
                item.payReduce = this.mathRound(item.payOriginalPrice - item.payPreferentialPrice);
                item.monthly = this.mathRound(item.payPreferentialPrice / 12);
                break;
            }
          });
        }
      },
      logout(value) {
        this.isLogout = value
      },
      updateSelected(selectedPackage) {
        // for update the child data
        this.pcSelectPackage = Object.assign({}, selectedPackage, {});
      },
      paymentChangeCycle(cycleItem) {
        this.selectBillingCycleFromPayment = cycleItem;
      },
      emailPasswordChange(info) {
        this.email = info.email;
        this.password = info.password;
      },
      scrollToWithAnimation(from = 0, to, duration) {
        if (!window.requestAnimationFrame) {
          window.requestAnimationFrame = (
            window.webkitRequestAnimationFrame ||
            window.mozRequestAnimationFrame ||
            window.msRequestAnimationFrame ||
            function (callback) {
              return window.setTimeout(callback, 1000 / 60);
            }
          );
        }
        const difference = Math.abs(from - to);
        const step = Math.ceil(difference / duration * 50);
        this.scrollStep(from, to, step);
      },
      scrollStep(start, end, step) {
        if (start === end) {
          return;
        }
        let d = (start + step > end) ? end : start + step;
        if (start > end) {
          d = (start - step < end) ? end : start - step;
        }
        window.scrollTo(d, d);
        window.requestAnimationFrame(() => this.scrollStep(d, end, step));
      },
      mathRound(num) {
        return Math.round(num * 100) / 100;
      },
      emailValidate(email) {
        const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
        return emailReg.test(email);
      },
      getDescription(description) {
        return this.$t(description)
          .split(/\n/)
          .filter(str => {
            return str.length > 0 && str.indexOf('<br') < 0;
          });
      },
      goToTermsService(){
        this.$router.push('../help-center/article/360036448091')
      }
    },
  };
</script>

