<template>
  <div class="cycle-container">
    <div class="cycle-content">
      <a v-for="(option, index) in selectBillingCycleList" v-on:click="onSelected(index, Object.assign(option, {updateList: undefined}))"
         class="cycle-item"
         :class="{'selected': selectedIndex === index}">{{$t(option.name)}}</a>
    </div>
    <a class="selected-bg" id="testtest" ref="selectedBg">{{$t(selectBillingCycleList[3].name)}}</a>

  </div>
</template>

<script>
  export default {
    name: 'PcCycleSwitch',
    data() {
      return {
        selectedIndex: 3,
        selectBillingCycleList: [
          {
            name: 'Monthly',
            cycleType: 'monthly'
          },
          {
            name: 'Quarterly',
            cycleType: 'quarterly'
          },
          {
            name: 'Semi-annually',
            cycleType: 'semiannually'
          },
          {
            name: 'Annually',
            cycleType: 'annually'
          }
        ],
        isInit: true
      };
    },
    props: {
      cycleChange: {}
    },
    watch: {
      cycleChange: {
        immediate: true,    // 
        handler(val) {
          if (this.selectBillingCycleList) {
            this.selectBillingCycleList.map((item, index) => {
              if (item.cycleType === val.cycleType) {
                this.onSelected(index, Object.assign(item, {updateList: false}));
              }
            });
          }
        }
      },
    },
    updated() {
      this.updateUi();
    },
    methods: {
      onSelected(index, option) {
        this.selectedIndex = index;
        this.updateUi();
        this.$emit('selected-index', index, option);
      },
      updateUi() {
        // calculate new position and width
        let offset = 0;
        let offsetLeft = document.getElementsByClassName('cycle-item')[this.selectedIndex].offsetLeft;
        let itemWidth = document.getElementsByClassName('cycle-item')[this.selectedIndex].clientWidth;
        let parentWidth = document.getElementsByClassName('cycle-content')[0].clientWidth;
        offset = parentWidth - offsetLeft - itemWidth + 2;
        document.getElementsByClassName('selected-bg')
          .item(0)
          .setAttribute('style', 'right:' + offset + 'px; width:' + document.getElementsByClassName('cycle-item')[this.selectedIndex].clientWidth + 'px');
      }
    }
  };
</script>

<style lang="scss">
  .cycle-container {
    height: 48px;
    .cycle-content {
      position: relative;
      border: 2px solid #E4EAF0;
      border-radius: 40px;
      height: 48px;
      display: flex;
      flex-direction: row;
      z-index: 1;
      align-items: center;
      a {
        font-size: 16px;
        padding: 0 28px;
        z-index: 1;
        color: #383E44;
      }
      .selected {
        color: #FFFFFF;
      }
    }
    .selected-bg {
      right: 2px;
      text-align: center;
      font-size: 16px;
      float: right;
      height: 44px;
      border-radius: 40px;
      padding: 0 28px;
      color: #138CFF;
      background-color: #138CFF;
      z-index: 0;
      bottom: 46px;
      position: relative;
      transition-property: right, width;
      transition-duration: 0.4s;

      transition-timing-function: ease;
      /* Safari */
      -webkit-transition-property: right, width;
      -webkit-transition-duration: 0.4s;

      -webkit-transition-timing-function: ease;
    }
  }
</style>
