<template>
  <div class="pay-content">
    <div class="pay-method">
      <div class="method" v-for="(method, index) in payMethodList" :key="method.id"
           @click="selectedPayment(index)" :class="{'method-selected': selectedMethod === index}">
        <div class="choose">
          <div class="pay-radio" :class="{'light': selectedMethod === index}">
            <i :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/hook-white.svg)'}"></i>
          </div>
          <div class="pay-detail">
            <div class="pay-logo">
              <i v-for="(icon, iconIndex) in method.logo" :key="icon" :class="icon"
                 :style="{backgroundImage:'url(' + method.logoUrl[iconIndex] + ')'}"></i>
            </div>
            <p class="method-name">{{$t(method.name)}}</p>
          </div>
        </div>
      </div>
    </div>
    <div>
      <div class="popover-arrow"></div>
      <div class="package-list">
        <div class="list-item">
          <p>{{$t('Plan')}}</p>
          <p>{{$t(packageInfo.name)}}</p>
        </div>
        <div class="list-item" v-if="selectBillingCycleListBuf.length > 0">
          <p>{{$t('Renewal Int')}}</p>
          <a-radio-group class="radio-group" @change="onChangeCycle" name="radioGroup" defaultValue="annually"
                         v-model="cycleType">
            <a-radio v-for="(item) in selectBillingCycleListBuf" :value="item.cycleType">{{$t(item.name)}}</a-radio>
          </a-radio-group>
        </div>
        <div class="list-item">
          <p>{{$t('Label Price')}} </p>
          <p>{{packageInfo.payOriginalPrice}} USD</p>
        </div>
        <div class="list-item">
          <p>{{$t('Discount')}}<span class="reduce-tips" v-if="isAnnually">{{$t('Bigger discount for longer subscription')}}</span>
          </p>
          <p class="reduce">- {{packageInfo.payReduce}} USD</p>
        </div>
        <div class="pay-line"></div>
        <div class="total">
          <p>{{$t('Total')}}</p>
          <p>{{packageInfo.payPreferentialPrice}} USD</p>
        </div>
        <a-button class="submit-btn" type="primary" :class="{'submit-btn-loading': iconLoading,'submit-btn-disabled': !emailBuf || !packageInfo.payOriginalPrice}" icon="poweroff"
                  :loading="iconLoading"
                  :disabled="!emailBuf || !packageInfo.payOriginalPrice"
                  @click="submitOrder">
          {{$t('Place Order')}}
        </a-button>
        <p class="error-tip" :class="{ 'error': !errorDisplay }" >{{errorDescription}}</p>
        <div class="foot-tips">
          <ul>
            <li>{{$t('By placing an order, you agree to our')}} <span @click="goToTermsService">{{$t('Terms of Service.')}}</span></li>
            <li>{{$t('The order is refundable within 30 days. See refund')}} <span @click="goToPolicyDetail">{{$t('policy for details.')}}</span>
            </li>
            <li>{{$t('Dont want our our newsletter, please contact Help@JetStream.Surf.')}}</li>
          </ul>

        </div>
      </div>
    </div>
    <div class="safe-tips">
      <div class="safe-logo">
        <i class="norton" :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/norton.svg)'}"></i>
        <i v-if="this.$i18n.locale !== 'en-US' " class="thirty-day-refund" :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/thirty-day-refund-zh.svg)'}"></i>
        <i v-if="this.$i18n.locale === 'en-US'" class="thirty-day-refund-us" :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/thirty-day-refund-us.svg)'}"></i>

      </div>
      <p>{{$t('Every JetStream plan')}}</p>
      <div class="features">
        <p>{{$t('has no bandwidth cap')}}</p>
        <p>{{$t('is refundable within 30 days')}}</p>
        <p>{{$t('comes with access to 38 global server clusters')}}</p>
        <p>{{$t('supports iOS, macOS, Android, Windows, Chromebook, routers, and Linux')}}</p>
        <p>{{$t('includes live human support 12 hours a day, 7 days a week')}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'PcPayment',
    data() {
      return {
        emailBuf: String,
        isLogin: !!localStorage.getItem('token'),
        passwordBuf: String,
        errorDisplay: false,
        errorDescription: this.$t('The email format is incorrect. Please fill in the format (name@example.com)'),
        buttonDisabled: false,
        selectedMethod: 0,
        cycleType: '',
        iconLoading: false,
        configoptions: {},
        payMethodList: [
          {
            id: 0,
            name: 'Alipay',
            logo: ['alipay'],
            logoUrl: [`${this.$assetsUrl}pay/alipay.svg`],
            type: 'payssionalipaycn'
          },
          {
            id: 1,
            name: 'Credit Card',
            logo: ['visa', 'master', 'unionpay', 'amex'],
            logoUrl: [
              `${this.$assetsUrl}pay/visa-pay.svg`,
              `${this.$assetsUrl}pay/master.svg`,
              `${this.$assetsUrl}pay/union-pay.svg`,
              `${this.$assetsUrl}pay/amex.svg`,
            ],
            type: 'stripe3dsecure'
          },
          {
            id: 2,
            name: 'PayPal',
            logo: ['paypal'],
            logoUrl: [
              `${this.$assetsUrl}pay/paypal.svg`
            ],
            type: 'paypalcheckout'
          },
          {
            id: 3,
            name: 'Crypto Currencies',
            logo: ['btc', 'btc2', 'eth', 'eth2'],
            logoUrl: [
              `${this.$assetsUrl}pay/btc.svg`,
              `${this.$assetsUrl}pay/btc2.svg`,
              `${this.$assetsUrl}pay/eth.svg`,
              `${this.$assetsUrl}pay/eth2.svg`,
            ],
            type: 'coinpayments'
          }
        ],
        androidPayMethodList: [
          {
            id: 1,
            name: 'Alipay',
            logo: ['alipay'],
          },
        ],
        packageInfo: this.selectedPackage,
        selectBillingCycleList: [
          {
            name: 'Monthly',
            cycleType: 'monthly'
          },
          {
            name: 'Quarterly',
            cycleType: 'quarterly'
          },
          {
            name: 'Semi-annually',
            cycleType: 'semiannually'
          },
          {
            name: 'Annually',
            cycleType: 'annually'
          }
        ],
        selectedBillCycleName: 'Annually',
        isAnnually: true,
        selectBillingCycleListBuf: [],

      };
    },
    computed: {
      // getPayMethodList() {
      //   return this.payMethodList = [
      //     {
      //       id: 1,
      //       name: this.$t('Alipay'),
      //       logo: ['alipay'],
      //       type: 'payssionalipaycn'
      //     },
      //     {
      //       id: 3,
      //       name: this.$t('Credit Card'),
      //       logo: ['visa', 'master', 'unionpay', 'amex'],
      //       type: 'stripe3dsecure'
      //     },
      //     {
      //       id: 4,
      //       name: this.$t('PayPal'),
      //       logo: ['paypal'],
      //       type: 'paypalcheckout'
      //     },
      //     {
      //       id: 5,
      //       name: this.$t('Crypto Currencies'),
      //       logo: ['btc', 'btc2', 'eth', 'eth2'],
      //       type: 'coinpayments'
      //     }
      //   ];
      // },
      // getSelectBillingCycleList() {
      //   return this.selectBillingCycleList = [
      //     {
      //       name: this.$t('Monthly'),
      //       cycleType: 'monthly'
      //     },
      //     {
      //       name: this.$t('Quarterly'),
      //       cycleType: 'quarterly'
      //     },
      //     {
      //       name: this.$t('Semi-annually'),
      //       cycleType: 'semiannually'
      //     },
      //     {
      //       name: this.$t('Annually'),
      //       cycleType: 'annually'
      //     }
      //   ];
      // },
    },
    props: {
      selectedPackage: {},
      buttonStatus: Boolean,
      email: String,
      password: String
    },
    watch: {
      selectedPackage: {
        immediate: true,    // 
        handler(val) {
          if (this.selectBillingCycleList && this.selectBillingCycleList.length > 0) {
            this.packageInfo = val;
            this.updateCycle(val.selectedCycle);
          }
        }
      },
      buttonStatus: {
        immediate: true,
        handler(val) {
          if (val instanceof Boolean) {
            this.buttonDisabled = val;
          }
        }
      },
      email: {
        immediate: true,
        handler(val) {
          this.emailBuf = val;
          this.errorDisplay = false;
          this.iconLoading = false;

        }
      },
      password: {
        immediate: true,
        handler(val) {
          this.passwordBuf = val;
          this.errorDisplay = false;
          this.iconLoading = false;

        }
      }
    },
    methods: {
      submitOrder() {
        this.errorDisplay = false
        if (this.emailBuf === '' || !this.emailValidate(this.emailBuf)) {
          this.errorDisplay = true;
          this.errorDescription = this.$t('The email format is incorrect. Please fill in the format (name@example.com)');
          return;
        }
        // console.log(`${window.location.origin}/${this.$i18n.locale}/order/generate-order`)
        this.iconLoading = true;
        this.configoptions = {};
        this.configoptions[`${this.packageInfo.userConfigoptions[0]}`] = this.packageInfo.userConfigoptions[1];
        this.configoptions[`${this.packageInfo.userConfigoptions[2]}`] = this.packageInfo.userConfigoptions[3];
        let requestParam = {
          'email': this.emailBuf,
          'paymentMethod': this.payMethodList[this.selectedMethod].type,
          'productId': this.packageInfo.pid,
          'billingcycle': this.packageInfo.selectedCycle,
          'aff': this.$route.query['aff'] || localStorage.getItem('aff') || '',
          'redirectTo': `${window.location.origin}/${this.$i18n.locale}/order/generate-order`,
          'configoptions': this.configoptions
        }
        if (this.isLogin) {
          const that = this
          this.$request.postOrders(requestParam, function (data) {
            that.iconLoading = false;
            if (data != null) {
              if (data.status === 201 || data.status === 200) {
                that.$router.push({
                  path: 'order/generate-order',
                  name: 'interchange',
                  params: {
                    invoiceId: data.data.invoiceId,
                    redirect: data.data.redirect
                  }
                });
                //window.open(data.data.redirect);
              } else {
                that.errorDescription = '';
              }
            } else {
              that.errorDescription = '';
            }
          });
        } else {
          let pushParam = {
            account: this.emailBuf,
            packages: this.packageInfo.name,
            cycle: this.$t(this.selectedBillCycleName),
            originalPrice: this.packageInfo.payOriginalPrice,
            reduce: this.packageInfo.payReduce,
            preferentialPrice: this.packageInfo.payPreferentialPrice,
            aff: this.$route.query['aff'] || localStorage.getItem('aff') || '',
            paymentMethod: this.payMethodList[this.selectedMethod].type,
            productId: this.packageInfo.pid,
            billingcycle: this.packageInfo.selectedCycle,
            redirectTo: `${window.location.origin}/${this.$i18n.locale}/order/generate-order`,
            configoptions: this.configoptions
          }
          this.$http.post(`${process.env.VUE_APP_SERVER_URL}/api/v1/orders/quickbuy`, requestParam)
                  .then(
                          (data) => {
                            this.iconLoading = false;
                            if (data && (data.status === 200 || data.status === 201)) {
                              this.$router.push({
                                path: '/order/confirm-order',
                                name: 'pay-success',
                                params: pushParam
                              });
                            } else {
                              if (data.message.indexOf('registered') > -1) {
                                this.errorDescription = '';
                              } else {
                                this.errorDescription = '';
                              }
                              this.errorDisplay = true;
                            }
                          })
                  .catch(reason => {
                    this.iconLoading = false;
                    this.errorDisplay = true;
                    this.errorDescription = reason.message;
                  });
        }
      },
      onChangeCycle(event) {
        let cycleItem;
        setTimeout(() => {
          this.selectBillingCycleListBuf.map(item => {
            if (item.cycleType === this.cycleType) {
              cycleItem = item;
            }
          });
          this.isAnnually = !(cycleItem.cycleType === 'annually');
          this.$emit('cycletype-change', cycleItem);
        }, 20);
      },
      selectedPayment(index) {
        this.selectedMethod = index;
        // set the arrow positon

        let offset = 110 * index + 46;
        document.getElementsByClassName('popover-arrow')
          .item(0)
          .setAttribute('style', 'top:' + offset + 'px');
      },
      updateCycle(selectedCycle) {
        if (typeof this.packageInfo.updateList === 'undefined') {
          let indexBuf = 0;
          this.selectBillingCycleList.map((item, index) => {
            if (item.cycleType === selectedCycle) {
              indexBuf = index;
              this.selectedBillCycleName = item.name;
              // display the reduce-tips
              this.isAnnually = !(item.cycleType === 'annually');
            }
          });
          this.selectBillingCycleListBuf = this.selectBillingCycleList.slice(indexBuf);
        }

        this.cycleType = selectedCycle;
      },
      emailValidate(email) {
        const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
        return emailReg.test(email);
      },
      goToTermsService(){
         window.open(`${window.location.origin}/help-center/article/360036448091`);
      },
      goToPolicyDetail(){
         window.open(`${window.location.origin}/help-center/article/360004852651`);
      }
    }
  };
</script>

