<template>
    <div class="login-content">
        <div class="login-form">
            <div class="input-wrap">
                <span>
                    {{$t("Your email")}}
                </span>
                <div class="email-row">
                    <a-input :placeholder="$i18n.t('Enter your email address')" size="large" @change="textChange" v-model="email" :disabled="isLogin"/>
                    <p class="friendly-tip" v-if="isLogin || isRegistered">{{$t('Your email has been registered. Service will be activated after the payment')}}</p>
                </div>
            </div>
            <div class="input-wrap" v-if="showPassword">
                <span>
                    {{$t("Password")}}
                </span>
                <a-input :placeholder="$i18n.t('Choose a strong password')" size="large" @change="textChange" v-model="password" type="password"/>
            </div>
        </div>
        <p>
            {{$t('We will not disclose your information')}}
        </p>
    </div>
</template>

<script>
  export default {
    name: 'PcLogin',
    data() {
      return {
        email: '',
        password: '',
        isLogin: !!localStorage.getItem('token'),
        isRegistered: false,
        //timeout: null,
      };
    },
    props: {
      showPassword: true,
      isLogout: false
    },
    watch: {
      isLogout: {
         immediate: true,
         handler(val) {
           this.email = localStorage.getItem('email');
           this.isLogin = !!localStorage.getItem('token');
         }
       },
      email: {
        handler(value) {
          if (this.emailValidate(this.email)) {
             //clearTimeout(this.timeout);
             //this.timeout = setTimeout(() => {
               this.checkEmail();
             //}, 1000);
          } else {
            this.isRegistered = false;
          }
        }
      }
    },
    methods: {
      textChange() {
        this.$emit('inputChange', {email: this.email.trim(), password: this.password.trim()})
      },
      emailValidate(email) {
        const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
        return emailReg.test(email);
      },
      checkEmail() {
        const that = this;
        this.$request.emailCheck(this.email.trim(), function (data) {
          if (data.status === 201 || data.status === 200) {
            if (data.data) {
              that.isRegistered = true;
            } else {
              that.isRegistered = false;
            }
          } else {
            that.isRegistered = false;
          }
        });
      }
    }
  };
</script>

