<template>
  <div>
    <div class="package-container">
      <a-spin class="product-detail-loading" v-if="packageList.length === 0">
        <a-icon slot="indicator" type="loading" style="font-size: 70px" spin/>
      </a-spin>
      <div class="package-wrap" v-for="(item, index) in packageList" :key="item.id"
           @click="stepOneClick(index)">
        <div class="package-content" :class="{'select-package': selectedPackageIndex === index
        , 'recommend-package': item.pid === 21}">
          <div class="radio" :class="{'light': selectedPackageIndex === index}" @click="stepOneClick(index)">
            <i :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/hook-white.svg)'}"></i>
          </div>
          <a-tooltip placement="bottomRight" arrowPointAtCenter>
            <template slot="title">
              <p>{{$t("30-day money-back guarantee")}}</p>
              <p>{{$t('Data usage resets monthly')}}</p>
              <p>{{$t('Unconsumed data do not carry forward')}}</p>
            </template>
            <div class="question" :style="{backgroundImage: 'url(' + $assetsUrl + 'pay/question.svg)'}">
            </div>
          </a-tooltip>
          <div class="package-info" @click="stepOneClick(index)">
            <div class="title">
              {{$t(item.name)}}
            </div>
            <p class="description" v-for="str in getDescription(item.description)">
              {{str}}
            </p>
            <!--<p class="description" v-html="$t(item.description)"></p>-->
            <div class="price-content">
              <span class="price">{{item.monthly}}</span>
              <span>/ {{$t('mo')}}</span>
            </div>
          </div>
          <div class="recommand-wrap" @click="stepOneClick(index)">
            <div class="recommand">{{item.recommend}}% {{$t('users chose this plan')}}</div>
            <div class="price">
              <div class="before" v-if="item.originalPrice > item.preferentialPrice">
                $ {{item.originalPrice}}
              </div>
              <div class="after">
                $ {{item.preferentialPrice}} / {{$t('billed every year')}}
              </div>
            </div>
          </div>
        </div>
        <div class="tips" v-if="selectedPackageIndex === index" @click="stepOneClick(index)" :class="{'recommend-package': item.pid === 21}">
          <ul>
            <li>{{$t('Pay annually for the best price')}}</li>
            <li>{{$t('30-day money-back guarantee')}}</li>
            <li>{{$t('Buy more uncapped bandwidth and increase connection limit later')}}</li>
          </ul>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  /* eslint-disable */
  export default {
    components: {},
    data() {
      return {
        selectedPackageIndex: 1,
        selectedPackage: {},
        packageList: Array,
        selectedCycle: String,
        configoptions: Array,
        deviceNumber: 2,
        bandwidthNumber: 0
      };
    },
    name: 'PcProducts',
    props: {
      dataList: Array,
      cycle: {
        name: '',
        cycleType: 'annually',
        updateList: true
      }
    },
    watch: {
      dataList: {
        immediate: true,    // 
        handler(val) {
          this.packageList = val;
          this.handleData();
        }
      },
      cycle: {
        immediate: true,
        handler(val) {
          this.cycle = val;
          if (this.packageList.length > 0) {
            this.calculatePrice(this.cycle.cycleType);
            this.handlePackageInfo(this.selectedPackageIndex);
          }
        }
      }
    },

    methods: {
      handleData() {
        if (this.packageList && this.packageList.length > 0) {
          this.packageList.map((x, index) => {


            switch (x.pid) {
              case 23:
                x.name = '200G UNLIMITED';
                x.description = 'First 200G data at uncapped speeds';
                break;
              case 21:
                x.name = '100G UNLIMITED';
                x.description = 'First 100G data at uncapped speeds';
                break;
              case 22:
                x.name = '50G UNLIMITED';
                x.description = 'First 50G data at uncapped speeds';
                break;
              default:
                break;
            }

            // setup the recommend
            if (x.name.indexOf('50G') > -1) {
              x['recommend'] = 10;
            } else if (x.name.indexOf('100G') > -1) {
              x['recommend'] = 66;
            } else {
              x['recommend'] = 24;
            }
          });

          // default
          this.calculatePrice('annually');
          this.handlePackageInfo(this.selectedPackageIndex);
          this.$emit('handle-done');
        }
      },
      stepOneClick(e) {
        this.selectedPackageIndex = e;
        this.calculatePrice(this.cycle.cycleType);
        // packageInfo
        this.handlePackageInfo(e);
      },
      handlePackageInfo(e) {
        this.selectedPackage = this.packageList[this.selectedPackageIndex];
        // for payment
        this.packageList[this.selectedPackageIndex]['selectedCycle'] = this.selectedCycle;
        this.$emit('select-update', Object.assign(this.packageList[this.selectedPackageIndex], {
          updateList: this.cycle.updateList,
          userConfigoptions: this.configoptions
        }));
      },
      calculatePrice(cycleType) {
        this.selectedCycle = cycleType;
        this.configoptions = [];
        this.packageList.map((item, index) => {
          // get the device limit price
          let devicePrice = 0;
          let devicePriceMonthly = 0;
          let bandwidth = 0;
          let bandwidthMonthly = 0;

          item['configoptions']['configoption'].map(config => {
            if (config.name.indexOf('Device Limit') > -1) {
              devicePrice = Number(config['options']['option'][0]['pricing']['USD'][cycleType]) * this.deviceNumber;
              devicePriceMonthly = Number(config['options']['option'][0]['pricing']['USD']['monthly']) * this.deviceNumber;
              // get the device limit and addon bandwidth
              if (index === this.selectedPackageIndex) {
                this.configoptions[0] = config.id;
                this.configoptions[1] = this.deviceNumber;
              }
            }
            if (config.name.indexOf('Addon Bandwidth') > -1) {
              bandwidth = Number(config['options']['option'][0]['pricing']['USD'][cycleType]) * this.bandwidthNumber;
              bandwidthMonthly = Number(config['options']['option'][0]['pricing']['USD']['monthly']) * this.bandwidthNumber;
              if (index === this.selectedPackageIndex) {
                this.configoptions[2] = config.id;
                this.configoptions[3] = this.bandwidthNumber;
              }
            }
          });
          // calculate the price
          switch (cycleType) {
            case 'monthly':
              item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
              item.preferentialPrice = this.mathRound(item.originalPrice);
              item.payReduce = 0;
              item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly) + devicePriceMonthly + bandwidthMonthly);
              item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.monthly) + devicePrice + bandwidth);
              item.monthly = item.payPreferentialPrice;
              break;
            case 'semiannually':
              item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
              item.preferentialPrice = this.mathRound(Number(item.pricing.USD.semiannually * 2) + devicePrice * 2 + bandwidth * 2);
              item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly * 6) + devicePriceMonthly * 6 + bandwidthMonthly * 6);
              item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.semiannually) + devicePrice + bandwidth);
              item.payReduce = this.mathRound(item.payOriginalPrice - item.payPreferentialPrice);
              item.monthly = this.mathRound(item.payPreferentialPrice / 6);
              break;
            case 'quarterly'://
              item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
              item.preferentialPrice = this.mathRound(Number(item.pricing.USD.quarterly * 4) + devicePrice * 4 + bandwidth * 4);
              item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly * 3) + devicePriceMonthly * 3 + bandwidthMonthly * 3);
              item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.quarterly) + devicePrice + bandwidth);
              item.payReduce = this.mathRound(item.payOriginalPrice - item.payPreferentialPrice);
              item.monthly = this.mathRound(item.payPreferentialPrice / 3);
              break;
            case 'annually':
              item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
              item.preferentialPrice = this.mathRound(Number(item.pricing.USD.annually) + devicePrice + bandwidth);
              item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
              item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.annually) + devicePrice + bandwidth);
              item.payReduce = this.mathRound(item.payOriginalPrice - item.payPreferentialPrice);
              item.monthly = this.mathRound(item.payPreferentialPrice / 12);
              break;
          }
        });
        // just for update the UI
        this.packageList = this.packageList.filter(item => {
          return true;
        });
      },
      getDescription(description) {
        return this.$t(description).split(/\n/)
          .filter(str => {
            return str.length > 0 && str.indexOf('<br') < 0;
          });
      },
      mathRound(num) {
        return Math.round(num * 100) / 100;
      }
    }
  };
</script>

